  .feedback__inner-wrapper {
    flex-direction: row;

    position: relative;
  }
}

@media (min-width: @desktop-width) {
  .feedback__wrapper {
    width: @desktop-width - 100px;
    padding: 0 50px;
    padding-top: 110px;
    padding-bottom: 165px;

    background-position: left 50px top 30px;
  }

  .feedback__wrapper::after {
    content: "";
    position: absolute;
    left: 50px;
    bottom: 10px;

    width: @desktop-width - 100px;
    height: 23px; // 23px;
  }
}

@media (min-width: @tablet-width) {
  .feedback__content-wrapper {
    width: 668px;
  }

  .feedback__action-wrapper {
    position: absolute;
    right: 0;
  }
}

@media (min-width: @desktop-width) {
  .feedback__content-wrapper {
    width: 1050px;
    padding-left: 175px;
  }
}


/* блок с отзывом */

.feedback__header {
  display: inline-block;
  margin: 0;
  padding: 0;
  padding-bottom: 24px;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;

  color: @header-color;

  background: transparent;
}

.feedback__list {
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;

  background: transparent;

  list-style: none;
}

@media (min-width: @desktop-width) {
  .feedback__list {
    width: 770px;
  }
}

.feedback__item {
  display: flex;
  width: 100%;
  margin: 0;
  padding: 0;
  flex-direction: column;

  background: transparent;
}

.feedback__text {
  display: block;
  margin: 0;
  padding: 0;
  padding-bottom: 27px;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;

  color: @main-text-color;

  background: transparent;
}

.feedback__user-name {
  display: block;
  margin: 0;
  padding: 0;
  padding-bottom: 3px;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  letter-spacing: 0.2px;
  font-weight: 700;

  color: @header-color;

  background: transparent;
}

.feedback__user {
  display: block;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 30px;
  font-weight: 400;

  color: @main-text-color;

  background: transparent;

  text-decoration: none;
}


@media (min-width: @tablet-width) {
  .feedback__header {
    padding-bottom: 50px;

    font-size: 43px;
    line-height: 50px;
  }

  .feedback__text {
    padding-bottom: 55px;

    font-size: 17px;
    line-height: 30px;
  }

  .feedback__user-name {
    font-size: 17px;
    line-height: 24px;
    padding-bottom: 5px;
  }

  .feedback__user {
    font-size: 17px;
    line-height: 30px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__header {
    font-size: 53px;
    line-height: 37px;
    letter-spacing: -1.2px;

    padding-bottom: 60px;
  }

  .feedback__text {
    padding-bottom: 37px;
  }

  .feedback__user-name {
    letter-spacing: 0.2px;
  }
}


/* блок кнопок вперед-назад и написать */

.feedback__action-wrapper {
  display: flex;
  width: 260px;
  flex-direction: column;
  justify-content: space-between;
}

.feedback__action-btn-wrapper {
  margin: 0;
  padding: 0;
  margin-bottom: 37px;

  order: 0;
  display: flex;
  justify-content: space-between;
}

.feedback__nav {
  display: block;
  width: 130px;
  height: 50px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  cursor: pointer;
  border: 1px solid #ececec;
  outline: none;

  background-color: @background-color-main;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.feedback__write-us {
  order: 1;
  width: 260px;
}


@media (min-width: @tablet-width) {
  .feedback__action-wrapper {
    width: 175px;
    position: absolute;
    right: 0;

    height: 100%;
  }

  .feedback__action-btn-wrapper {
    order: 1;
    margin-bottom: 33px;
  }

  .feedback__write-us {
    width: 175px;

    order: 0;
  }
}
