/* заголовок */

.product-week__header-wrapper {
  display: flex;
  flex-direction: column;
  width: 260px;
  margin: 0;
  padding: 0;
  padding-top: 28px;
  padding-bottom: 23px;
}

.product-week__header {
  display: inline-block;
  order: 2;
  margin: 0;
  padding: 0;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;

  color: #63d1bb;
}

.product-week__title {
  display: inline-block;
  order: 1;
  margin: 0;
  padding: 0;
  padding-bottom: 5px;

  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  letter-spacing: 0.3px;
  font-weight: 700;

  color: @header-color;
}

@media (min-width: @tablet-width) {
  .product-week__header-wrapper {
    flex-direction: row;
    width: @tablet-width - 100px;
    margin: 0;
    padding: 0;
    padding-top: 41px;
    padding-bottom: 45px;

    justify-content: space-between;
  }

  .product-week__header {
    width: 80px;
    padding-right: 25px;
    font-size: 17px;
    line-height: 17px;


    align-self: center;
    text-align: center;
  }

  .product-week__title {
    font-size: 43px;
