  flex-direction: column;

  justify-content: space-between;
  position: relative;
}

.product-week__img {
  position: absolute;
  //left: 50%;
  bottom: 125px;
  width: 260px;

  font-size: 0;
  line-height: 0;

  background-color: @background-color-product-card;
}

