  .product-week__card-wrapper {
    flex-direction: row;
  }

  .product-week__img {
    position: absolute;
    bottom: 0;
    right: 0;
    align-self: flex-end;
    width: 333px;
    height: auto;

    background-color: @background-color-product-card;
  }
}

@media (min-width: @desktop-width) {
  .product-week__img {
    position: absolute;
    bottom: 0;
    right: 0;
    align-self: flex-end;
    width: 525px;

    background-color: @background-color-product-card;
  }
}


