/* блок */
.about-us {
  width: auto;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}
.about-us__wrapper {
  width: 260px;
  margin: 0 auto;
  padding: 0 30px;
  padding-top: 30px;
  padding-bottom: 15px;
}
@media (min-width: 768px) {
  .about-us__wrapper {
    width: 668px;
    padding: 0 50px;
    padding-top: 40px;
    padding-bottom: 5px;
  }
}
@media (min-width: 1150px) {
  .about-us__wrapper {
    width: 700px;
    padding: 0 225px;
    padding-top: 55px;
  }
}
.about-us__header {
  display: inline-block;
  width: 260px;
  margin: 0;
  padding: 0;
  padding-bottom: 30px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .about-us__header {
    width: 335px;
    padding-bottom: 45px;
    font-size: 43px;
    line-height: 50px;
    letter-spacing: -0.7px;
  }
}
@media (min-width: 1150px) {
  .about-us__header {
    width: 525px;
    padding-bottom: 45px;
    font-size: 53px;
    line-height: 57px;
  }
}
.about-us__list {
  display: flex;
  width: 100%;
  margin: 0;
  padding: 0;
  flex-direction: column;
  background-color: #ffffff;
  list-style: none;
}
@media (min-width: 768px) {
  .about-us__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .about-us__item:nth-child(1) {
    order: -6;
  }
  .about-us__item:nth-child(2) {
    order: -4;
  }
  .about-us__item:nth-child(3) {
    order: -2;
  }
  .about-us__item:nth-child(4) {
    order: -5;
  }
  .about-us__item:nth-child(5) {
    order: -3;
  }
  .about-us__item:nth-child(6) {
    order: -1;
  }
}
.about-us__item {
  display: inline-block;
  width: 260px;
  margin: 0;
  padding: 0;
  padding-top: 55px;
  padding-bottom: 28px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.2px;
  font-weight: 700;
  color: #000000;
  background-color: #ffffff;
  position: relative;
}
.about-us__item .about-us__item-svg {
  position: absolute;
  top: 0;
  left: 0;
  fill: #63d1bb;
}
.about-us__item .about-us__item-svg--eko {
  top: 2px;
  left: 8px;
}
.about-us__item .about-us__item-svg--price {
  top: 2px;
}
.about-us__item .about-us__item-svg--like {
  top: 6px;
  left: 8px;
}
.about-us__item .about-us__item-svg--clew {
  top: 3px;
  left: 5px;
}
.about-us__item .about-us__item-svg--rocket {
  top: 5px;
  left: -4px;
}
.about-us__item .about-us__item-svg--box {
  top: 3px;
  left: 3px;
}
.about-us__item:last-child {
  padding-bottom: 0;
}
@media (min-width: 768px) {
  .about-us__item {
    width: 333px;
    padding-bottom: 58px;
    font-size: 20px;
    line-height: 22px;
  }
}
@media (min-width: 1150px) {
  .about-us__item {
    width: 350px;
  }
}
.button {
  display: inline-block;
  width: 256px;
  margin: 0;
  padding: 14px 0;
  box-sizing: border-box;
  text-align: center;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #ffffff;
  border: 2px solid #444444;
  outline: none;
}
.button:hover,
.button:focus {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.catalog {
  width: auto;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}
.catalog__wrapper {
  display: flex;
  width: 260px;
  margin: 0 auto;
  padding: 30px;
  padding-bottom: 0;
  flex-direction: column;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .catalog__wrapper {
    width: 668px;
    padding: 0 50px;
    padding-top: 77px;
    padding-bottom: 55px;
  }
}
@media (min-width: 1150px) {
  .catalog__wrapper {
    width: 1050px;
    padding: 0 50px;
    padding-top: 132px;
  }
}
.catalog__list {
  display: flex;
  width: 260px;
  margin: 0 auto;
  padding: 0;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  list-style: none;
}
@media (min-width: 768px) {
  .catalog__list {
    width: 668px;
    padding: 0;
  }
}
@media (min-width: 1150px) {
  .catalog__list {
    width: 1050px;
    padding: 0;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: wrap;
  }
}
.catalog__item {
  display: flex;
  width: 260px;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 0;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  box-shadow: 0 0 3px 0 #ececec;
}
.catalog__item:hover .catalog__item-wrapper {
  box-shadow: 0 5px 10px #ececec;
}
.catalog__item:active .catalog__item-wrapper {
  box-shadow: 0 0 5px #ececec;
}
.catalog__item:last-child {
  padding-bottom: 0;
}
@media (min-width: 768px) {
  .catalog__item {
    width: 668px;
    padding: 0;
    margin-bottom: 53px;
    flex-direction: row;
    justify-content: flex-start;
  }
  .catalog__item:last-child {
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .catalog__item {
    width: 315px;
    margin: 0;
    margin-left: 51px;
    margin-bottom: 60px;
    padding: 0;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }
  .catalog__item:nth-child(3n+1) {
    margin-left: 0;
  }
  .catalog__item:last-child {
    margin-bottom: 60px;
  }
}
.catalog__item-wrapper {
  display: flex;
  padding: 0 30px;
  padding-bottom: 29px;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .catalog__item-wrapper {
    width: 335px;
    min-height: 335px;
    padding-bottom: 0;
    padding-left: 27px;
  }
}
@media (min-width: 1150px) {
  .catalog__item-wrapper {
    width: 258px;
    min-height: 195px;
  }
}
/* блок с картикой товара */
.catalog__item-img-link {
  text-decoration: none;
  line-height: 0;
  font-size: 0;
}
.catalog__item-img {
  width: 260px;
  height: 195px;
  background-color: #ececec;
}
@media (min-width: 768px) {
  .catalog__item-img {
    width: 335px;
    height: 335px;
  }
}
@media (min-width: 1150px) {
  .catalog__item-img {
    width: 315px;
    height: 465px;
  }
}
/* блок с информацией для карточки товара */
.catalog__item-header-link {
  text-decoration: none;
}
.catalog__item-header {
  display: block;
  width: 200px;
  margin: 0;
  padding: 0;
  padding-top: 26px;
  padding-bottom: 5px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .catalog__item-header {
    width: 100%;
    padding-bottom: 6px;
    font-size: 20px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .catalog__item-header {
    padding-top: 24px;
    padding-bottom: 4px;
  }
}
.catalog__item-description {
  display: block;
  margin: 0;
  padding: 0;
  padding-bottom: 19px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 13px;
  line-height: 30px;
  font-weight: 400;
  color: #444444;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .catalog__item-description {
    font-size: 15px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .catalog__item-description {
    padding-bottom: 0;
  }
}
.catalog__to-cart {
  display: flex;
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 22px;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.catalog__to-cart::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 2px;
  background-color: #ececec;
}
.catalog__to-cart a {
  margin-right: -3px;
}
@media (min-width: 768px) {
  .catalog__to-cart {
    width: 267px;
    padding: 27px 0;
    padding-left: 5px;
  }
  .catalog__to-cart::before {
    top: 2px;
    left: 6px;
    width: 200px;
    height: 2px;
  }
}
@media (min-width: 1150px) {
  .catalog__to-cart {
    width: 255px;
    padding: 27px 0;
    padding-left: 0;
  }
}
.catalog__item-price {
  display: block;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  background-color: #ffffff;
}
.catalog__to-cart-logo-svg {
  width: 21px;
  height: 18px;
  opacity: 0.3;
}
.catalog__to-cart-logo:hover .catalog__to-cart-logo-svg {
  fill: #63d1bb;
  opacity: 1;
}
.catalog__to-cart-logo:active .catalog__to-cart-logo-svg {
  fill: #63d1bb;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .catalog__to-cart::before {
    width: 265px;
  }
  .catalog__item-price {
    font-size: 17px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .catalog__to-cart::before {
    width: 255px;
  }
}
